





























	export default {
    data() {
        return {
			imageList:[],
			appVersion: "",
			detail: "",
			deviceInfo: "",
			deviceType: "",
			deviceVersion: "",
			imgPath: "",
			userMobile: ""
		}
    },
	computed: {
	    Screen_width() {
	        return uni.getSystemInfoSync().windowWidth;
	    },
		Screen_height() {
		    return uni.getSystemInfoSync().windowHeight;
		}
	},
    methods: {
        chooseImage(){
			var that = this
			var token = uni.getStorageSync('token');
			uni.chooseImage({
				count: 6, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function (res) {
					console.log(res.tempFilePaths)
					const tempFilePaths = res.tempFilePaths;
					that.imageList = that.imageList.concat(res.tempFilePaths);
					uni.uploadFile({
						url: 'http://api.xinmicredit.cn/file/upLoad', //仅为示例，非真实的接口地址
						method:'POST',
						filePath: tempFilePaths[0],
						header:{'Authorization': token},
						name: 'multipartFile',
						/* formData: {
							multipartFile: that.imageList
						}, */
						success: (res) => {
							// console.info(res);
							// console.info(res.data);
							var list = JSON.parse(res.data);
							console.info(list.data);
							this.imgPath = list.data;
						}
					});
				}
			});
		},
		fankui(){
			var token = uni.getStorageSync('token');
			var phone = uni.getStorageSync('phone');
			uni.getSystemInfo({
				success: (res) => {
					this.appVersion = res.platform;
					this.deviceVersion = res.system;
					this.deviceInfo = res.model;
					this.deviceType = res.brand;				
				}
			})
			uni.request({
				url:'http://api.xinmicredit.cn/feedback/add',
				method:'POST',
				header:{'Authorization': token},
				data:{
					"appVersion": this.appVersion,
					"detail": this.detail,
					"deviceInfo": this.deviceInfo,
					"deviceType": this.deviceType,
					"deviceVersion": this.deviceVersion,
					"imgPath": this.imgPath,
					"userMobile": phone
				},
				success: (res) => {
					console.info(res);
					uni.showToast({
						title:'感谢反馈。。'
					})
				}
			})
		}
    }
}
